<style scoped>
    .type {
        margin-bottom: 10px;
    }
    .page {
        margin-top: 10px;
        float: left;
    }
    .clear:before,.clear:after {
        content:"";
        display:table;
    }
    .clear:after { 
        clear:both;
     }
     .all-pay {
         margin-top: 10px;
         float: right;
     }
</style>
<template>
   <div>
       <RadioGroup class="type" @on-change="changeType" v-model="type" type="button">
            <Radio label="all">全部</Radio>
            <Radio label="4">IP</Radio>
            <Radio label="5">CPU</Radio>
            <Radio label="6">硬盘</Radio>
            <Radio label="7">内存</Radio>
            <Radio label="8">带宽</Radio>
            <Radio label="9">防护</Radio>
        </RadioGroup>
        <Table :columns="columns" :data="data" @on-selection-change="selectPayState" @on-select-cancel="selectPayCancelState"></Table>
        <div class="clear">
            <Page class="page" @on-change="toPage" :total="allData.length" />
            <Button class="all-pay" @click="allPay" type="primary">全部支付</Button>
        </div>
        
        <Modal
            v-model="renewal_fee"
            title="续费服务器"
            @on-ok="ok"
            @on-cancel="cancel">
            <Select placeholder="请选择续费时长" v-model="renewal_fee_config.price" style="width:100%">
                <Option v-for="item in renewal_fee_config.price_options" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
            <Input v-model="renewal_fee_config.order_note" type="textarea" placeholder="备注" />
        </Modal>
        <Modal
        v-model="pay_type_select"
        title="支付方式"
        @on-ok="pay_type_ok"
        @on-cancel="pay_type_cancel">
            <Select placeholder="请选择付款方式" v-model="pay_type_config.pay" style="width:100%">
                <Option v-for="item in pay_type_config.pay_options" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
        </Modal>
   </div>
</template>
<script>
    import axios from "axios";
    import { BASE_URL, PROTOCOL } from "../../config/index.js";
    let globalData = [];
    export default {
         data () {
            return {
                pay_type_select: false,
                pay_type_config: {
                    serial_number: "",
                    pay: 1,
                    order_param: {

                    },
                    pay_options: [
                        {
                            value: 1,
                            label: "余额"
                        },
                        {
                             value: 2,
                            label: "支付宝"
                        }
                    ]
                },
                renewal_fee: false,
                renewal_fee_config: {
                    price: 1,
                    order_note: "",
                    price_options: [
                        {
                            value: 1,
                            label: "一个月"
                        },
                        {
                            value: 6,
                            label: "半年"
                        },
                        {
                            value: 12,
                            label: "一年"
                        }
                    ],
                    renewal_fee_param: {

                    }
                },
                type: "all",
                columns: [
                    {
                        type: 'selection',
                        width: 60,
                        align: 'center'
                    },
                    {
                        title: '订单号',
                        key: 'order_sn'
                    },
                    {
                        title: '购买时长',
                        key: 'duration'
                    },
                    {
                        title: '机器编号',
                        key: 'machine_sn'
                    },
                    {
                        title: '资源类型',
                        key: 'resource_type'
                    },
                    {
                        title: '支付状态',
                        key: 'order_status'
                    },
                    {
                        title: '订单类型',
                        key: 'order_type'
                    },
                    {
                        title: '支付方式',
                        key: 'pay_type'
                    },
                    {
                        title: '应付金额',
                        key: 'payable_money'
                    },
                    {
                        title: '操作',
                        key: 'action',
                        width: 170,
                        align: 'center',
                        render: (h, params) => {
                            let operat = [
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.show(params.row);
                                        }
                                    }
                                }, '查看')
                            ];
                            if(params.row.order_status=="已支付") {
                                if(location.search.indexOf("?business_sn")>-1) {
                                    operat.push(h('Button', {
                                            props: {
                                                type: 'primary',
                                                size: 'small'
                                            },
                                            style: {
                                                marginRight: '5px'
                                            },
                                            on: {
                                                click: () => {
                                                    this.renewal_fee = true;
                                                    this.renewal_fee_config.renewal_fee_param = params.row;
                                                }
                                            }
                                    }, '续费'));
                                }
                            }
                            if(params.row.order_status=="正在支付") {
                                operat.push(h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.pay_type_select = true;
                                            this.pay_type_config.serial_number = params.row.serial_number;
                                            this.pay_type_config.order_param = params.row;
                                        }
                                    }
                                }, '支付'));
                            }
                            if(params.row.order_status=="待支付") {
                                operat.push(h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            axios.post(PROTOCOL+"//"+BASE_URL+"/home/customer/makeTrade",{
                                                order_id: [params.row.id],
                                                coupon_id: 0
                                            }).then(res => {
                                                if(res.data.code==1) {
                                                    // alert("支付成功");
                                                    //  this.getData();
                                                    this.pay_type_select = true;
                                                    this.pay_type_config.serial_number = res.data.data;
                                                    this.pay_type_config.order_param = params.row;
                                                } else {
                                                    alert(res.data.msg);
                                                }
                                            });
                                        }
                                    }
                                }, '支付'));
                                operat.push(h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        // marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            let isOk = confirm("是否要删除此订单？");
                                            if(isOk) {
                                                axios.get(PROTOCOL+"//"+BASE_URL+"/home/customer/delOrder",{
                                                    params: {
                                                         order_id: params.row.id
                                                    }
                                                }).then(res => {
                                                    if(res.data.code==1) {
                                                        alert("删除成功");
                                                         this.getData();
                                                    } else {
                                                        alert(res.data.msg);
                                                    }
                                                });
                                            }
                                        }
                                    }
                                }, '删除'));
                            }
                            return h('div',operat);
                        }
                    }
                ],
                data: [
                ],
                allData: [],
                page: 0,
                pageTotal: 10,
            }
        },
        methods: {
            selectPayState(selection) {
                if(selection.length==this.data.length) {
                    globalData = this.allData;
                }else if(selection.length==0) {
                    globalData = [];
                }else {
                    globalData = globalData.concat(selection.filter(item => {
                        if(!globalData.find(e => e.id==item.id)) {
                            return true;
                        }
                    }));
                }
            },
            selectPayCancelState(selection,row) {
                globalData.splice(globalData.findIndex(item => item.id==row.id),1);
            },
            allPay() {
                axios.post(PROTOCOL+"//"+BASE_URL+"/home/customer/makeTrade",{
                    order_id: [...globalData.map(item => item.id)],
                    coupon_id: 0
                }).then(res => {
                    if(res.data.code==1) {
                        // alert("支付成功");
                        //  this.getData();
                        this.pay_type_select = true;
                        this.pay_type_config.serial_number = res.data.data;
                        this.pay_type_config.order_param = {
                            payable_money: globalData.reduce((result,item) => {
                               result += Number(item.payable_money);
                               return result;
                            },0)
                        };
                    } else {
                        alert(res.data.msg);
                    }
                });
            },
            pay_type_ok() {
                let params = this.pay_type_config.order_param;
                if(this.pay_type_config.pay==1) {
                    let isOk = confirm(`此订单需要扣掉${params.payable_money}元余额，是否要支付？`);
                    if(isOk) {
                        axios.get(PROTOCOL+"//"+BASE_URL+"/home/customer/payTradeByBalance",{
                            params: {
                                serial_number: this.pay_type_config.serial_number
                            }
                        }).then(res => {
                            if(res.data.code==1) {
                                // console.log(res.data.data);
                                alert("付款成功");
                                this.getData();
                            } else {
                                alert(res.data.msg);
                            }
                        });
                    }
                } else {
                    let isOk = confirm(`此订单需要扣掉${params.payable_money}元，是否要支付？`);
                    if(isOk) {
                        location.href = PROTOCOL+"//"+BASE_URL+"/home/customer/payTradeByAli?serial_number="+this.pay_type_config.serial_number+"&way=web";
                    }
                }
                
            },
            pay_type_cancel() {

            },
            ok () {
                // this.$Message.info('Clicked ok');
                axios.post(PROTOCOL+"//"+BASE_URL+"/home/customer/renewresource",{
                    business_number: this.renewal_fee_config.renewal_fee_param.business_sn,
                    order_sn: this.renewal_fee_config.renewal_fee_param.order_sn,
                    price: this.renewal_fee_config.renewal_fee_param.money,
                    length: this.renewal_fee_config.price,
                    resource_type: this.renewal_fee_config.renewal_fee_param.type,
                    order_note: this.renewal_fee_config.order_note
                }).then(res => {
                    if(res.data.code==1) {
                        alert("续费成功");
                        this.getData();
                    } else {
                        alert(res.data.msg);
                    }
                });
            },
            cancel () {
                // this.$Message.info('Clicked cancel');
            },
            changeType(type) {
                if(type=="all") {
                    this.getData();
                }else {
                    this.getData({
                        resource_type: type
                    });
                }
            },
            toPage(page) {
                // console.log(page);
                let start = (page - 1) * this.pageTotal;
                let end = (page - 1) * this.pageTotal + this.pageTotal;
                console.log(globalData);
                this.data = this.allData.sort((a,b) => b.id - a.id).slice(start,end).map(item => {
                    if(globalData.find(e => e.id==item.id)) {
                        item._checked = true;
                    } else {
                        item._checked = false;
                    }
                    return item;
                });
            },
             show (data) {
                this.$Modal.info({
                    title: '详细信息',
                    content: `
                        订单号: ${data.order_sn}<br />
                        扣款后余额：${data.after_money}<br />
                        扣款前余额：${data.before_money}<br />
                        业务编号：${data.business_sn}<br />
                        业务员: ${data.business_name}<br />
                        购买时长: ${data.duration}<br />
                        机器编号: ${data.machine_sn}<br />
                        备注: ${data.order_note}<br />
                        支付状态: ${data.order_status}<br />
                        订单类型: ${data.order_type}<br />
                        实际金额: ${data.pay_price}<br />
                        支付时间: ${data.pay_time}<br />
                        支付方式: ${data.pay_type}<br />
                        应付金额: ${data.payable_money}<br />
                        资源单价: ${data.price}<br />
                        资源详情: ${data.resource}<br />
                        资源类型: ${data.resource_type}<br />
                        支付流水号: ${data.serial_number}<br />
                        `
                })
            },
            getData(param = {}) {
                this.data = [];
                this.allData = [];
                if(location.search.indexOf("?business_sn") > -1) {
                    param.business_sn = location.search.substr(1).split("=")[1];
                }
                 axios.get(PROTOCOL+"//"+BASE_URL+"/home/customer/orderList",{
                     params: param
                 }).then(res => {
                    if(res.data.code==1) {
                        this.allData = res.data.data;
                        this.data = this.allData.sort((a,b) => b.id - a.id).slice(this.page,this.pageTotal);
                    }else if(res.data.code==5000) {
                        location.href = PROTOCOL+"//"+BASE_URL+"/tz/login.html";
                    } else {
                        console.error(res.data.msg);
                    }
                });
            }
        },
        mounted() {
            this.getData();
        }
    }
</script>