<style scoped>
    .header {
        margin-bottom: 10px;
        overflow: hidden;
    }
    .page {
        margin-top: 10px;
    }
    .fl {
        float: left;
    }
    .fr {
        float: right;
    }
    .mt {
       margin-top: 10px; 
    }
    .operat {
        text-align: right;
    }
    .send {
        margin-top: 10px;
    }
    .conversation-content-item {
        margin: 5px 0;
    }
    .conversation-content-item .date,.conversation-content-item .content {
        display: block;
    }
    .conversation-content-item.self .date {
        color: blue;
    }
    .conversation-content-item.allochromatic .date {
        color: green;
    }
    .conversation-content-item .content {
        margin-left: 10px;
    }
</style>
<template>
    <div>
        <div class="header">
            <RadioGroup class="type fl" @on-change="changeType" v-model="type" type="button">
                <Radio label="0">待处理</Radio>
                <Radio label="1">处理中</Radio>
                <Radio label="2">完成</Radio>
                <Radio label="3">取消</Radio>
            </RadioGroup>
            <Button class="post fr" @click="ShowWorkOrderModal" type="primary">提交工单</Button>
        </div>
        
        <Table :columns="columns" :data="data"></Table>
        <Page class="page" @on-change="toPage" :total="allData.length" />
        <Modal
            v-model="workOrderModal"
            title="提交工单"
            :loading="workOrderModalLoading"
            @on-ok="ok"
            @on-cancel="cancel">
            <Row :gutter="5">
                <Col span="4">
                    <div style="text-align: center;line-height: 32px;">
                        工单分类
                    </div>
                </Col>
                <Col span="10">
                    <Select v-model="currentType" @on-change="getSubType" style="width: 100%;">
                        <Option v-for="item in typeList" :value="item.id" :key="item.id">{{ item.type_name }}</Option>
                    </Select>
                </Col>
                <Col span="10">
                    <Select v-model="currentType2" @on-change="changeSelectTypeId" v-if="typeSubList.length" style="width: 100%;">
                        <Option v-for="item in typeSubList" :value="item.id" :key="item.id">{{ item.type_name }}</Option>
                    </Select>
                </Col>
            </Row>
            <Row class="mt" :gutter="5">
                <Col span="4">
                    <div style="text-align: center;line-height: 32px;">
                        主机编号
                    </div>
                </Col>
                <Col span="20">
                    <Select v-model="business" style="width: 100%;">
                        <Option v-for="item in businessList" :value="item.business_number" :key="item.business_number">{{ item.machine_number }}</Option>
                    </Select>
                </Col>
            </Row>
            <Row class="mt" :gutter="5">
                <Col span="24">
                    <Input v-model="workOrderContent" type="textarea" placeholder="工单内容" />
                </Col>
            </Row>
        </Modal>
        <Modal v-model="conversation" footer-hide fullscreen :title="conversationParam.title">
            <div :style="container_content_style" ref="container">
                <div ref="containerContent">
                    <div class="conversation-content-item" :class="{'self': item.answer_role == 1,'allochromatic': item.answer_role == 2}" v-for="item in conversationParam.content">
                        <span class="date">{{item.created_at}}</span>
                        <span class="content">{{item.answer_content}}</span>
                    </div>
                </div>
            </div>
            <Input v-model="conversationParam.content_push" @on-enter="sendConversationContent" type="textarea" placeholder="要发布的内容" />
            <div class="operat">
                <Button type="primary" @click="sendConversationContent" class="send">发布</Button>
            </div>
        </Modal>
    </div>
</template>
<script>
    import axios from "axios";
    import { BASE_URL, PROTOCOL } from "../../config/index.js";
    import io from 'socket.io-client';
    let socket;
    export default {
         data () {
            return {
                container_content_style: {
                    overflow: 'auto',
                    height: (window.innerHeight - 64 - 110) + 'px',
                },
                conversation: false,
                conversationParam: {
                    title: "普通对话框",
                    content: [],
                    content_push: "",
                    conversationData: {}
                },
                workOrderModal: false,
                workOrderModalLoading: true,
                type: "0",
                currentType: 0,
                currentType2: 0,
                selectTypeId: 0,
                business: "",
                typeList: [

                ],
                typeSubList: [

                ],
                businessList: [

                ],
                workOrderContent: "",
                columns: [
                    {
                        title: '工单号',
                        key: 'work_order_number'
                    },
                    {
                        title: '机器编号',
                        key: 'machine_number'
                    },
                    {
                        title: '工单状态',
                        key: 'workstatus'
                    },
                    {
                        title: '工单类型',
                        key: 'worktype'
                    },
                    {
                        title: '处理部门',
                        key: 'department'
                    },
                    {
                       title: '业务类型',
                        key: 'business_type' 
                    },
                    {
                        title: '操作',
                        key: 'action',
                        width: 170,
                        align: 'center',
                        render: (h, params) => {
                            let operat = [
                                h('Button', {
                                props: {
                                    type: 'primary',
                                    size: 'small'
                                },
                                style: {
                                    marginRight: '5px'
                                },
                                on: {
                                    click: () => {
                                        this.show(params.row);
                                    }
                                }
                                }, '查看')
                            ];
                            if(params.row.work_order_status==1) {
                                operat.push(h('Button', {
                                props: {
                                    type: 'primary',
                                    size: 'small'
                                },
                                style: {
                                    marginRight: '5px'
                                },
                                on: {
                                    click: () => {
                                        this.conversationParam.conversationData = params.row;
                                        this.openConversation(params.row);
                                    }
                                }
                                }, '联系'));
                                
                            }
                            return h('div',operat);
                        }
                    }
                ],
                data: [
                ],
                page: 0,
                pageTotal: 11,
                allData: [],
                socketOpen: false
            }
        },
        methods: {
            sendConversationContent() {
                if(this.conversationParam.content_push) {
                    axios.post(PROTOCOL+"//"+BASE_URL+"/home/customer/insert_work_answer",{
                        work_number: this.conversationParam.conversationData.work_order_number,
                        answer_content: this.conversationParam.content_push
                    }).then(res => {
                        if(res.data.code==1) {
                            this.conversationParam.content_push = "";
                            socket.emit("client_to_admin",res.data.data);
                            this.conversationParam.content.push(res.data.data);
                            setTimeout(() => {
                                this.$refs.container.scrollTop = this.$refs.containerContent.offsetHeight;
                            },500);
                        } else {
                            console.error(res.data.msg);
                        }
                    });
                }
            },
            openConversation(data) {
                this.conversation = true;
                this.conversationParam.title = data.machine_number+"主机的"+data.worktype+"问题";
                axios.get(PROTOCOL+"//"+BASE_URL+"/home/customer/show_work_answer",{
                    params: {
                        work_number: data.work_order_number
                    }
                }).then(res => {
                    if(res.data.code==1) {
                        // socket.on('connect',  ()=>{
                        //     // console.log('client connect server');
                            
                        // });

                        let {business} = res.data.data;
                        this.conversationParam.content = res.data.data.content;
                        setTimeout(() => {
                            this.$refs.container.scrollTop = this.$refs.containerContent.offsetHeight;
                        },500);
                        // console.log(`to_id:${business.customer_id}work_num:${business.work_order_number}`);
                        if(!this.socketOpen) {
                            socket.on(`to_id:${business.customer_id}work_num:${business.work_order_number}`,content=>{
                                console.log(content);
                                this.conversationParam.content.push(content);
                                setTimeout(() => {
                                    this.$refs.container.scrollTop = this.$refs.containerContent.offsetHeight;
                                },500);
                            });
                            this.socketOpen = true;
                        }
                        
                    } else {
                        console.error(res.data.msg);
                    }
                });
            },
            ShowWorkOrderModal() {
                this.workOrderModalLoading = true;
                this.workOrderModal = true;
            },
            changeSelectTypeId(id) {
                this.selectTypeId = id;
            },
            getSubType(value) {
                this.changeSelectTypeId(value);
                this.getType({
                    parent_id: value
                },"typeSubList");
            },
             ok () {
                 if(this.typeSubList.length!=0&&!this.currentType2) {
                     this.$Message.info('请选择二级分类');
                     this.workOrderModalLoading = false;
                     return ;
                 }
                 axios.post(PROTOCOL+"//"+BASE_URL+"/home/customer/insert_work_order",{
                     business_num: this.business,
                     work_order_type: this.selectTypeId,
                     work_order_content: this.workOrderContent
                 }).then(res => {
                     if(res.data.code==1) {
                         this.getData({
                            work_order_status: this.type
                        });
                         this.$Message.info('工单提交成功');
                         this.workOrderModal = false;
                     } else {
                         this.$Message.info(res.data.msg);
                         this.workOrderModalLoading = false;
                     }
                 });
            },
            cancel () {
                // this.$Message.info('Clicked cancel');
            },
            changeType(type) {
                if(type=="all") {
                    this.getData();
                }else {
                    this.getData({
                        work_order_status: type
                    });
                }
            },
            toPage(page) {
                // console.log(page);
                let start = (page - 1) * this.pageTotal;
                let end = (page - 1) * this.pageTotal + this.pageTotal;
                this.data = this.allData.sort((a,b) => b.id - a.id).slice(start,end);
            },
            show (data) {
                this.$Modal.info({
                    title: '详细信息',
                    content: `
                        工单号: ${data.work_order_number}<br />
                        业务编号: ${data.business_num}<br />
                        工单内容: ${data.work_order_content}<br />
                        工单状态: ${data.workstatus}<br />
                        完成时间: ${data.complete_time}<br />
                        工单类型: ${data.worktype}<br />
                        处理部门: ${data.department}<br />
                        业务类型: ${data.business_type}<br />
                        机器编号: ${data.machine_number}<br />
                    `
                })
            },
            getData(param = {}) {
                    this.data = [];
                    this.allData = [];
                    axios.get(PROTOCOL+"//"+BASE_URL+"/home/customer/show_work_order",{
                        params: param
                    }).then(res => {
                        if(res.data.code==1) {
                            this.allData = res.data.data;
                            this.data = this.allData.sort((a,b) => b.id - a.id).slice(this.page,this.pageTotal);
                        } else {
                            console.error(res.data.msg);
                        }
                    });
            },
            getType(param = {},dataAttr="typeList") {
                axios.get(PROTOCOL+"//"+BASE_URL+"/home/customer/work_types",{
                    params: param
                }).then(data => {
                    if(data.data.code==1) {
                        this[dataAttr] = data.data.data;
                    } else {
                        console.error(data.data.msg);
                    }
                });
            },
            getBusinessList(param = {}) {
                axios.get(PROTOCOL+"//"+BASE_URL+"/home/customer/businessList",{
                    params: param
                }).then(data => {
                    if(data.data.code==1) {
                        this.businessList = data.data.data;
                    } else {
                        console.error(data.data.msg);
                    }
                });
            }
        },
        mounted() {
            socket = io(PROTOCOL+"//"+BASE_URL+":8120");
            this.getData({
                work_order_status: 0
            });
            this.getType();
            this.getBusinessList();
        }
    }
</script>